import countryArr from "./../components/country.json";
export default {
  data: function data() {
    return {
      customUrl: process.env.VUE_APP_BASEURL,
      checkList: [],
      authcode: "",
      sendMsgDisabled: false,
      canSaveEdit: false,
      canSendAuthCode: false,
      canSendAuthCodeAgain: false,
      econfirmSuccess: false,

      /*验证成功判断*/
      time: 0,
      failTx: "",
      isPhone: false,
      checkedPhone: false,
      checked: true,
      checked1: true,
      checked2: false,
      checked3: false,
      loading: false,
      changeUserEmailShow: false,
      countryArr: [],
      userEmail: "",
      form: {
        name: "",
        country: "",
        address: "",
        location: "",
        phone: "",
        email: "",
        weChat: "",
        skype: "",
        line: "",
        QQ: "",
        wangwang: "",
        whatsapp: "",
        receivePhone: '',
        allowService: '',
        allowNews: '',
        phoneCountry: ''
      },
      formDefault: "{}",
      tip: '',
      isGift: false
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.$Burying({
      object: '10001'
    });
    this.formDefault = JSON.stringify(this.form); //格式化国家

    Object.keys(countryArr).forEach(function (key) {
      Object.keys(countryArr[key]).forEach(function (key2) {
        _this.countryArr.push({
          label: countryArr[key][key2].nameEN,
          value: countryArr[key][key2].code2
        });
      });
    });
    var t = setInterval(function () {
      if (_this.$store.state.userInfo) {
        clearInterval(t);
        _this.form.name = _this.$store.state.userInfo.name;
        _this.form.weChat = _this.$store.state.userInfo.weChat;
        _this.form.skype = _this.$store.state.userInfo.skype;
        _this.form.whatsapp = _this.$store.state.userInfo.whatsapp;
        _this.form.wangwang = _this.$store.state.userInfo.wangwang;
        _this.form.QQ = _this.$store.state.userInfo.QQ;
        _this.form.line = _this.$store.state.userInfo.line;
        _this.form.country = _this.$store.state.userInfo.country;
        _this.form.location = _this.$store.state.userInfo.location;
        _this.form.address = _this.$store.state.userInfo.address;
        _this.form.phone = _this.$store.state.userInfo.mobile;
        _this.form.email = _this.$store.state.userInfo.email;
        _this.checkedPhone = _this.$store.state.userInfo.receivePhone == 1 ? true : false;
        _this.checked2 = _this.$store.state.userInfo.allowService == 1 ? true : false;
        _this.checked3 = _this.$store.state.userInfo.allowNews == 1 ? true : false;

        if (!_this.form.whatsapp && !_this.form.skype) {
          _this.tip = "Complete your Skype or WhatsApp details to get rewards";
        } else {
          _this.tip = "You have completed the Skype or WhatsApp, you can receive a free gift package.";
        }

        if (_this.$store.state.userInfo.getSkypeWhatsappFree == true) {
          _this.isGift = true;
        }
      }
    }, 500);
  },
  methods: {
    changeUserPhone: function changeUserPhone() {
      this.isPhone = true;
      this.changeUserEmail();
    },
    // 获取验证码
    sendAuthCode: function sendAuthCode() {
      var _this2 = this;

      this.$showLoading();
      var vm = this;
      vm.sendMsgDisabled = true;
      vm.canSendAuthCode = false;
      vm.canSendAuthCodeAgain = true;

      if (this.isPhone == false) {
        // 邮箱验证码
        this.$apiCall("api.User.checkEmail", {
          email: vm.userEmail
        }, function (r) {
          _this2.$hideLoading();

          if (r.ErrorCode == '9999') {
