            _this2.$message({
              message: r.Message,
              type: "error"
            });

            vm.sendMsgDisabled = false;
            vm.canSendAuthCode = true;
            vm.canSendAuthCodeAgain = false;
          }
        });
      }
    },
    changeUserEmail: function changeUserEmail() {
