        }
      } else {
        this.canSendAuthCode = false;
        this.$refs.verify.reset();
        this.econfirmSuccess = false;
        this.failTx = this.isPhone == false ? 'Please fill in the correct Email address' : 'Please fill in the correct Mobile phone';
      }
    },
    save: function save() {
